import {Randomfood,SEARCH,AddtoCart,RemoveCart} from "../Constants"
const initialState = {
    randomData:[],
    searchs:[],
    AddCart:[],
}
export function FetchRandom(state=initialState ,{type,payload}){
switch (type) {
    case Randomfood:
        return {...state , randomData:payload};
    default:
        return state

}
}
export function FetchSEARCH(state=initialState ,{type,payload}){
switch (type) {
    case SEARCH:
        return {...state , searchs:payload};
    default:
        return state

}

}
export function AddCartItem(state=initialState ,action){
    switch (action.type) {
    case AddtoCart:
     const {idMeal,strMeal,strMealThumb} = action.payload;
     let vals = {
        idMeal:idMeal,
        strMeal:strMeal,
        strMealThumb:strMealThumb,
     }
        return {
                ...state,
       AddCart:[...state.AddCart ,vals],
        };
    default:
        return state

}
}
export function RemoveCartitem(state=initialState ,{type,payload}){
  switch (type) {
    case RemoveCart:
console.log(payload)
         const updatedCart = state.AddCart.filter((item) => item.strMeal == payload);
      return {
        ...state,
        AddCart: updatedCart,
      };
    default:
        return state

}

}