import {Randomfood,SEARCH,AddtoCart,RemoveCart } from "../Constants"
export const RandomFetch = (data)=>{
    return {
        type:Randomfood,
        payload:data,
           }
}
export const SEARCHFetch = (data)=>{
    return {
        type:SEARCH,
        payload:data,
           }
}
export const ADDTOCART = (idMeal,strMeal,strMealThumb)=>{
    return {
        type:AddtoCart,
        payload:{idMeal,strMeal,strMealThumb},
           }
}

export const REMOVECART = (strMeal)=>{
    return {
        type:RemoveCart ,
        payload:`${strMeal}`,
           }
}